//import {login} from '@/pages/utils/request'
import gotoLogin from '@/pages/components/login'
import {mapState} from 'vuex'
let startY = 0, moveY = 0, pageAtTop = true;
export default {
	components:{
		gotoLogin,
	},
	onShow(){
		console.log('???')
		if(this.hasLogin){
			this.avator = this.userInfo.avatarUrl
			this.showLogin = ""
		}
	},
	mounted() {
		this.getDrawUserInfo()
	},
	computed:{
		...mapState(['userInfo','hasLogin'])
	},
	data() {
		return {
			avator :'/static/user1.png',
			userNum:'1388  0546  901',
			camera :'/static/camera.png',
			specClass: 'none',
			listData: [{
				title: "会员积分",
				icon:"iconhuiyuanjifen1",
				url:"",
				id: 1 ,
				},{
				title: "我的卡券",
				icon:"iconwodeqiaquan",
				url:"/pages/card/card",
				id:2 ,
				},{
				title: "我的活动",
				icon:"iconwodehuodong",
				url:"/pages/promotion/promotion",
				id:3 ,
				},{
				title: "投诉建议",
				icon:"icontousujianyi-copy",
				url:"/pages/suggest/suggest",	
				id:4 ,
				},{
				title: "设置",
				icon:"iconshezhi",
				url:"/pages/memberedit/memberedit",
				id:5,
			}],
			memeberList :[{
				title: "积分" ,
				data: 99999 ,
				url:"/pages/point/point"
				},{
				title: "等级",
				data: "高级会员" ,
				url:"/pages/point/point"
				},{
				title: "优惠券",
				data: "查看" ,
				url:"/pages/card/card"
			}],
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			moveDis: 0 ,
			nomemeber: false,
			showLogin: "",
			
		}
	},
	methods: {

		/*
		* 绘制图形
		*/
		 getDrawUserInfo () {
			 const ctx = uni.createCanvasContext('userinfo' , this)
			 ctx.save()
			 ctx.beginPath() //开始创建一个路径
			 ctx.arc(45, 45, 27, 0, 2 * Math.PI, false) //画一个圆形裁剪区域
			 ctx.clip() //裁剪
			 ctx.drawImage(this.avator , 15,15,65,65) //绘制图片
			 ctx.restore() //恢复之前保存的绘图上下文
			 ctx.draw()
			 ctx.setFillStyle('rgb(210,210,210)')
			 ctx.setFontSize(16)
			 ctx.fillText('NO.',5 ,160)
			 ctx.setFontSize(12)
			 ctx.fillText(this.userNum ,35 ,160)
			 ctx.draw(true)
			 

		 },

		 coverTouchstart(e){
			 if(pageAtTop === false){
				 return
			 }
			 this.coverTransition = 'transform .1s linear'
			 startY = e.touches[0].clientY
		 },
		coverTouchmove(e) {
			moveY = e.touches[0].clientY
			let moveDistance = moveY - startY
			this.moveDis = moveDistance
			if(moveDistance < 0){
				this.moving = false
				return
			}
			this.moving = true
			if(moveDistance >= 30 && moveDistance < 50){
				moveDistance = 30
			}
			if(moveDistance > 0 && moveDistance <= 40){
				this.coverTransform = `translateY(${moveDistance}px)`
			}
		},
		coverTouchend(){
			if(this.moving === false){
				return
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			 // setTimeout(()=>{
				//  this.coverTransform = 'translateY(0px)'
			 // },3000)
		},
		showCamera(){
			if(this.moveDis >=15){
				console.log(this.moveDis)
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
				this.coverTransform = 'translateY(0px)'
				this.moveDis = 0
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
				
				}
			}
		},
		stopPrevent(){},
		showFunction(item){
			if(item.id === 1) {
				if(this.hasLogin) {
					this.nomemeber = true
					return
				}
				uni.showToast({
					title: '需要先登录',
					icon:'none'
				})
				setTimeout(() => {
					this.showLogin ="login"
				}, 1000)
				
			}
			else{
				if(this.hasLogin) {
					uni.navigateTo({
						url: item.url
					})
					return
				}
				uni.showToast({
					title: '需要先登录',
					icon:'none'
				})
				setTimeout(() => {
					this.showLogin ="login"
				}, 1000)
			}
		 },
		 //关闭会员积分的页面
		 backtitle(){
			 this.nomemeber = false 
		 },
		 //跳转到拍摄小票的页面
		 gotopicture(){
			 console.log('Pp')
			 uni.navigateTo({
			 	url: '/pages/picture/picture'
			 })
		 },
		 showPoint(item ){
			 if(this.hasLogin){
				 uni.navigateTo({
				 	url: item.url
				 })
				 return
			 }
				uni.showToast({
					title: '需要先登录',
					icon:'none'
				})
				setTimeout(() => {
					this.showLogin ="login"
				}, 1000)
		 },
		 hiddendiag(){
		 	this.showLogin = ""
			uni.redirectTo({
				url:'/pages/my/my'
			})
		 },
	},
}
